    position: relative;
    overflow: hidden;
}

.gallery.style-2 .item {
    margin-bottom: 30px;
}

.gallery .item img {
    width: 100%;
}

.gallery .overlay .top {
    position: absolute;
    text-align: center;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 0%;
    background: rgba(0, 0, 0, 0.7);
    transition: .5s cubic-bezier(0.4, 0, 1, 1);
    opacity: 0;
    visibility: hidden;
}

.gallery .item:hover .overlay .top {
    height: 100%;
    transition: .5s cubic-bezier(0.4, 0, 1, 1);
    opacity: 1;
    visibility: visible;
}

.gallery .overlay .top .box {
    display: table;
    width: 100%;
    height: 100%;
}

.gallery .overlay .top .content {
    display: table-cell;
    vertical-align: middle;
}

.gallery .overlay .top a {
    position: relative;
    top: -35px;
    color: #fff;
    height: 50px;
    width: 50px;
    line-height: 50px;
    text-align: center;
    border-radius: 50%;
    background: rgba(255, 255, 255, 0.3);
}

.gallery .overlay .bottom {
    position: absolute;
    left: 10px;
    bottom: -100%;
    text-align: center;
    right: 10px;
    height: 60px;
    background: rgba(255, 255, 255, 0.3);
    color: #fff;
    opacity: 0;
    visibility: hidden;
    transition: .5s ease;
}

.gallery .item:hover .overlay .bottom {
    bottom: 10px;
    opacity: 1;
    visibility: visible;
    transition: .9s ease;
    transition-delay: .3s;
}

.gallery .overlay .bottom h4 {
    color: #fff;
    margin-top: 20px;
}

.gallery .img-box {
    position: relative;
    overflow: hidden;
}

.gallery .img-box .overlay {
    position: absolute;
    text-align: center;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 0%;
    background: rgba(141, 198, 63, 0.9);
    transition: .7s cubic-bezier(0.34, -0.13, 0.34, 1.1);
    opacity: 0;
    visibility: hidden;
}

.gallery .img-box .overlay .inner-box {
    position: relative;
    display: table;
    height: 100%;
    width: 100%;
    text-align: center;
}

