    position: relative;
    display: table-cell;
    vertical-align: middle;
}

.gallery .item:hover .overlay {
    height: 100%;
    transition: .7s cubic-bezier(0.34, -0.13, 0.34, 1.1);
    opacity: 1;
    visibility: visible;
}

