    font-family: 'Verdana', 'Oxygen', 'Open Sans', sans-serif;
    left: 0px;
    top: 0px;
    text-align: center;
    line-height: 80px;
    width: 100%;
    height: 100%;
    background: rgba(141, 198, 63, 0.9);
    color: #ffffff;
    opacity: 0;
    transition: .5s ease;
}

.best_sellers .best_selling_item .img_holder:hover a:after {
    opacity: 1;
    transition: .5s ease;
}


/*** 
=============================================
    Not found area style
=============================================
***/

.not-found-area {
    background: #ffffff none repeat scroll 0 0;
    padding-bottom: 100px;
    padding-top: 72px;
}

.not-found-content h1 {
    font-size: 150px;
    margin: 0 0 7px;
}

.not-found-content h3 {
    color: #222;
    font-size: 18px;
    font-weight: 400;
    margin: 0 0 20px;
    font-family: 'Verdana', 'Oxygen', 'Open Sans', sans-serif;
}

.not-found-content p {
    font-size: 18px;
    margin: 0 0 42px;
}

.not-found-content .search-form {
    max-width: 432px;
    position: relative;
}

.not-found-content .search-form input[type="text"] {
    background: #fff;
    border: 1px solid #f1f1f1;
    color: #9a9a9a;
    display: inline-block;
    font-size: 16px;
    font-weight: 300;
    height: 55px;
    letter-spacing: 1px;
    padding-left: 20px;
    padding-right: 60px;
    position: relative;
    transition: all 500ms ease 0s;
    width: 432px;
}

.not-found-content .search-form button {
    background: #8dc63f none repeat scroll 0 0;
    border: medium none;
    color: #fff;
    display: inline-block;
    height: 55px;
    padding: 11px 0;
    position: absolute;
    right: 0;
    text-align: center;
    top: 0;
    transition: all 500ms ease 0s;
    width: 55px;
}

.not-found-content .search-form button i {
    font-size: 14px
}

.not-found-content .search-form input[type="text"]:focus {
    border: 1px solid #8dc63f;
    background: #fff;
    color: #000;
}

.not-found-content .search-form input[type="text"]:focus+button,
.not-found-content .search-form button:hover {
    background: #8dc63f none repeat scroll 0 0;
    color: #fff;
}


/*** 

====================================================================
    Testimonials Section style
====================================================================

***/

.testimonials-section2 {
    position: relative;
}

.testimonials-section2 .slide-item {
    border: 1px solid #f4f4f4;
    padding: 30px 20px 25px;
    transition: .5s ease;
    margin-bottom: 15px;
}

.testimonials-section2 .slide-item:hover {
    transition: .5s ease;
    box-shadow: 0px 9px 17px 5px rgba(0, 0, 0, 0.03);
}

article.slide-item.center {}

.testimonials-section2 .slide-text span {
    color: #f4f4f4;
    font-size: 40px;
    transition: .5s ease;
}

.testimonials-section2 .slide-item:hover .slide-text span {
    color: #8dc63f;
    transition: .5s ease;
}

.testimonials-section2 .slide-text p {
    padding-top: 14px;
}

.testimonials-section2 .rating {
    position: relative;
    max-width: 220px;
    margin: 15px auto;
}

.testimonials-section2 .rating:before {
    position: absolute;
    content: "";
    right: 0;
    width: 62px;
    height: 1px;
    top: 10px;
    background: #eaeaea;
}

.testimonials-section2 .rating:after {
    position: absolute;
    content: "";
    left: 0;
    width: 62px;
    height: 1px;
    top: 10px;
    background: #eaeaea;
}

.testimonials-section2 .rating li {
    position: relative;
    color: #ababab;
    display: inline-block;
    font-size: 14px;
}

.testimonials-section2 .author h4 {
    font-size: 22px;
    margin-bottom: 5px;
}

.testimonials-section2 .author p {
    color: #8dc63f;
}

.testimonials-section2 .donate-us h2 {
    margin-top: 160px;
    margin-bottom: 30px;
}


