/* Tooltip text */

.remove-btn .tooltiptext {
    visibility: hidden;
    width: 120px;
    background-color: black;
    color: #fff;
    text-align: center;
    padding: 5px 0;
    border-radius: 6px;
    /* Position the tooltip text - see examples below! */
    position: absolute;
    z-index: 1;
}


/* Show the tooltip text when you mouse over the tooltip container */

.remove-btn:hover .tooltiptext {
    visibility: visible;
}

.login-link {
    position: absolute;
    color: green;
    top: 20px;
    right: 40px;
    font-size: 20px;
    font-family: 'Verdana', 'Oxygen', 'Open Sans', sans-serif;
}

.login-link:hover {
    color: #8dc63f;
}

@-webkit-keyframes appear {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes appear {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@-webkit-keyframes slideIn {
    0% {
        -webkit-transform: translateX(-2%);
                transform: translateX(-2%);
    }
    100% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}

@keyframes slideIn {
    0% {
        -webkit-transform: translateX(-2%);
                transform: translateX(-2%);
    }
    100% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}

@-webkit-keyframes shrink {
    0% {
        width: 95%;
    }
    100% {
        width: 90%;
    }
}

@keyframes shrink {
    0% {
        width: 95%;
    }
    100% {
        width: 90%;
    }
}

