/*============= accordion ===============*/

.accordion-box {}

.accordion {
    margin-bottom: 20px;
    position: relative;
}

.accordion .acc-btn .left-icon {
    position: absolute;
    left: 0;
    top: 0;
    background: #f1f1f1;
    width: 60px;
    height: 100%;
    color: #9c9c9c;
    font-size: 20px;
    text-align: center;
}

.accordion .acc-btn.active .left-icon {
    color: #fff;
}

.accordion .left-icon span {
    position: relative;
    top: 18px;
}

.accordion .acc-btn {
    cursor: pointer;
    font-size: 18px;
    font-weight: 600;
    padding: 12px 30px;
    margin: 0px;
    padding-right: 50px;
    border: 1px solid #f4f4f4;
}

.accordion .acc-btn.active {
    border-color: #8dc63f;
    border-bottom: 0px;
}

.accordion .acc-btn p {
    color: #303030;
    font-size: 16px;
    font-family: 'Verdana', 'Oxygen', 'Open Sans', sans-serif;
}

.accordion .acc-btn.active p {}

.accordion .acc-btn.pr {
    position: relative;
}

.accordion .acc-btn .img-box {
    display: none;
}

.accordion .acc-btn.active .img-box {
    display: block;
}

.accordion .acc-btn .toggle-icon {
    color: #c8c8c8;
    display: block;
    height: 24px;
    font-size: 14px;
    line-height: 25px;
    position: absolute;
    right: 15px;
    top: 13px;
    width: 25px;
    border-left: 1px solid #c8c8c8;
}

.accordion .acc-btn .toggle-icon.left {
    color: #9c9c9c;
    display: block;
    font-weight: 100;
    height: 24px;
    line-height: 24px;
    position: absolute;
    right: 20px;
    bottom: 20px;
    left: 30px;
}

.accordion .acc-btn .toggle-icon .plus:before {
    transition: all 0.3s ease 0s;
    top: 0;
    right: 0;
    position: absolute;
    line-height: 25px;
}

.accordion-box .accordion .acc-btn.active .toggle-icon .plus {
    opacity: 0;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
}

.accordion-box .accordion .acc-btn .toggle-icon .minus:before {
    transition: all 0.3s ease 0s;
    color: #8dc63f;
    position: absolute;
    right: 0;
    top: 0;
    line-height: 25px;
}

.accordion-box .accordion .acc-btn .toggle-icon .minus {
    opacity: 0;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
}

.accordion-box .accordion .acc-btn.active .toggle-icon .minus {
    opacity: 1;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
}

.accordion-box .accordion .acc-content {
    color: #9c9c9c;
    background: #f7f7f7;
    border: 1px solid #8dc63f;
    display: none;
    padding: 20px 20px;
    border-top: 0px;
}

.accordion-box .accordion .acc-content.collapsed {
    display: block;
}

.accordion-box .accordion .acc-content p {
    margin: 0;
}

.accordion-box .accordion .acc-btn.active .toggle-icon .minus {
    color: #121d2f;
}


/*=== style-2 ==========*/

