/*** 

====================================================================
  donate Popup
====================================================================

***/

.donate-popup {
    position: fixed;
    left: 0px;
    top: -100%;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.80);
    z-index: 99999999;
    visibility: hidden;
    opacity: 0;
    overflow: auto;
    transition: all 700ms ease;
    -moz-transition: all 700ms ease;
    -webkit-transition: all 700ms ease;
    -ms-transition: all 700ms ease;
    -o-transition: all 700ms ease;
}

.donate-popup.popup-visible {
    top: 0;
    visibility: visible;
    opacity: 1;
}

.donate-popup .close-donate {
    position: absolute;
    right: 0;
    top: 0;
    font-size: 0;
    color: #ffffff;
    cursor: pointer;
    z-index: 5;
    width: 100%;
    height: 120%;
    cursor: crosshair;
}

.donate-popup .close-donate:hover {
    opacity: 0.70;
