    position: relative;
    height: 7px;
    transition: .7s ease;
    padding: 35px 0 25px;
}

.urgent-cause2 .progress-box .bar {
    position: relative;
    width: 100%;
    height: 7px;
    background-color: #f7f7f7;
}

.urgent-cause2 .progress-box .bar-inner {
    position: relative;
    display: block;
    width: 0%;
    height: 7px;
    background-image: url(../images/resource/progress-bg.jpg);
    -webkit-transition: all 1500ms ease;
    -ms-transition: all 1500ms ease;
    -o-transition: all 1500ms ease;
    -moz-transition: all 1500ms ease;
    transition: all 1500ms ease;
}

.urgent-cause2 .progress-box .count-text {
    position: absolute;
    text-align: center;
    right: 0px;
    font-family: 'Verdana', 'Oxygen', 'Open Sans', sans-serif;
    margin-right: -23px;
    bottom: 15px;
    width: 46px;
    border-radius: 5px;
    height: 26px;
    background: #8dc63f;
    color: #ffffff;
    line-height: 26px;
    font-size: 13px;
    font-weight: 600;
    opacity: 0;
    -webkit-transition: all 1000ms ease;
    -ms-transition: all 1000ms ease;
    -o-transition: all 1000ms ease;
    -moz-transition: all 1000ms ease;
    transition: all 1000ms ease;
}

.urgent-cause2 .progress-box .bar-inner.counted .count-text {
    opacity: 1;
}

.urgent-cause2 .progress-box .count-text:after {
    content: '';
    position: absolute;
    left: 50%;
    margin-left: -4px;
