.gallery {
    position: relative;
}

.gallery .section-title h2 {
    color: #fff;
}

.gallery .item {
    position: relative;
    overflow: hidden;
}

.gallery.style-2 .item {
    margin-bottom: 30px;
}

.gallery .item img {
    width: 100%;
}

.gallery .overlay .top {
    position: absolute;
    text-align: center;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 0%;
    background: rgba(0, 0, 0, 0.7);
    transition: .5s cubic-bezier(0.4, 0, 1, 1);
    opacity: 0;
    visibility: hidden;
}

.gallery .item:hover .overlay .top {
    height: 100%;
    transition: .5s cubic-bezier(0.4, 0, 1, 1);
    opacity: 1;
    visibility: visible;
}

.gallery .overlay .top .box {
    display: table;
    width: 100%;
    height: 100%;
}

.gallery .overlay .top .content {
    display: table-cell;
    vertical-align: middle;
}

.gallery .overlay .top a {
    position: relative;
    top: -35px;
    color: #fff;
    height: 50px;
    width: 50px;
    line-height: 50px;
    text-align: center;
    border-radius: 50%;
    background: rgba(255, 255, 255, 0.3);
}

.gallery .overlay .bottom {
    position: absolute;
    left: 10px;
    bottom: -100%;
    text-align: center;
    right: 10px;
    height: 60px;
    background: rgba(255, 255, 255, 0.3);
    color: #fff;
    opacity: 0;
    visibility: hidden;
    transition: .5s ease;
}

.gallery .item:hover .overlay .bottom {
    bottom: 10px;
    opacity: 1;
    visibility: visible;
    transition: .9s ease;
    transition-delay: .3s;
}

.gallery .overlay .bottom h4 {
    color: #fff;
    margin-top: 20px;
}

.gallery .img-box {
    position: relative;
    overflow: hidden;
}

.gallery .img-box .overlay {
    position: absolute;
    text-align: center;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 0%;
    background: rgba(141, 198, 63, 0.9);
    transition: .7s cubic-bezier(0.34, -0.13, 0.34, 1.1);
    opacity: 0;
    visibility: hidden;
}

.gallery .img-box .overlay .inner-box {
    position: relative;
    display: table;
    height: 100%;
    width: 100%;
    text-align: center;
}

.gallery .img-box .overlay .inner-box .content-box {
    position: relative;
    display: table-cell;
    vertical-align: middle;
}

.gallery .item:hover .overlay {
    height: 100%;
    transition: .7s cubic-bezier(0.34, -0.13, 0.34, 1.1);
    opacity: 1;
    visibility: visible;
}

.gallery .overlay a {
    position: relative;
    color: #fff;
    height: 45px;
    width: 45px;
    border: 1px solid #fff;
    line-height: 43px;
    text-align: center;
    border-radius: 50%;
    transition: .5s ease;
}

.gallery .overlay a:hover {
    background: #fff;
    color: #8dc63f;
    transition: .5s ease;
}

.gallery .content h4 {
    font-size: 20px;
    color: #fff;
    margin: 15px 0 7px;
}

.gallery.grid-page .content h4,
.gallery.massonary-page .content h4 {
    color: #303030;
}

.gallery.massonary-page .content {
    padding: 20px;
    border: 1px solid #f4f4f4;
    border-top: 0px;
    padding-top: 0px;
    overflow: hidden;
}

.gallery .content p {
    font-size: 14px;
    color: #8dc63f;
}

.gallery .content .text p {
    color: #999;
}


/*======= gallery filter ==========*/

.post-filter {
    display: inline-block;
    margin: 0;
    right: 0;
    top: 0;
    margin-bottom: 30px;
    position: relative;
    z-index: 999;
}

.post-filter.style-3 {
    margin-top: -88px;
}

.gallery.grid-page {
    padding-top: 80px;
}

.massonary-page .post-filter {
    margin-bottom: 50px;
}

.post-filter li {
    padding: 0;
    margin-bottom: 20px;
}

.post-filter li span {
    color: #ffffff;
    font-size: 14px;
    line-height: 24px;
    display: block;
    margin: 0 5px;
    padding: 6px 20px;
    border: 1px solid #404733;
    background: #242d0f;
    border-radius: 18px;
    cursor: pointer;
    transition: .5s ease;
}

.post-filter.style-2 li span {
    background: transparent;
    color: #999;
    border-color: #f4f4f4;
}

.post-filter li:hover span,
.post-filter li.active span {
    background: #8dc63f;
    border-color: #8dc63f;
    transition: .5s ease;
    color: #fff;
}

.post-filter li span i {
    font-size: 14px;
    margin-right: 5px;
}


