}

.event-style1 {
    padding: 80px 0 40px;
    background-size: cover;
}

.event-style1 .section-title h2 {
    color: #666;
}

.event-style1 .item {
    position: relative;
    transition: .5s ease-in-out;
    margin-bottom: 50px;
    overflow: hidden;
    padding: 1em;
    border: 1px dashed #ddd;
}

.event-style1 .item.style-2 {
    margin-bottom: 28px;
}

.event-style1 .item.style-2 .img-column {
    width: 30%;
