}

.event-style1 .item.style-2 .clearfix {
    /* padding: 19px; */
}

.event-style1 .item .lower-content {
    position: relative;
    padding: 0px 19px 0;
    border-left: 0px;
}

.event-style1 .item.style-2 .lower-content {
    padding-top: 0px;
}

.event-style1 .item .date {
    position: absolute;
    right: 15px;
    top: 15px;
