    text-align: center;
}

.event-style1 .item .date span {
    display: block;
    color: #8dc63f;
    background: #333;
    margin-top: 5px;
    padding: 6px 0;
}

.event-style1 .item .lower-content .text p {
    margin-bottom: 25px;
    font-size: 14px;
    color: #666;
}

.event-style1 .item.style-2 .lower-content .text p {
    margin-bottom: 5px;
}

.event-style1 .item .lower-content h4 {
    font-weight: 400;
    margin-top: 5px;
    margin-bottom: 10px;
    line-height: 30px;
    transition: all 300ms ease;
    color: #3b7840;
    font-size: 22px;
}

.event-style1 .item .lower-content h4:hover {
    color: #8dc63f;
    transition: all 300ms ease;
}

.event-style1 .item .post-meta {
    color: #8dc63f;
