    padding: 17px 19px;
}

.event-style1 .item .post-meta li {
    margin-right: 10px;
}

.event-style1 .item .post-meta i {
    margin-right: 5px;
}

.event-style1 .item .lower-content .default_link {
    text-transform: uppercase;
    color: #222;
    font-weight: 700;
    transition: .5s ease;
}

.event-style1 .item .lower-content .default_link:hover {
    transition: .5s ease;
    color: #8dc63f;
}

.event-style1 .item .lower-content .default_link i {
    font-size: 11px;
}

.event-style1 .item .img-holder {
    display: block;
    overflow: hidden;
    position: relative;
    width: 100%;
    max-height: 350px;
}

.event-style1 .item .img-holder a {
    width: 100%;
    overflow: hidden;
}

.event-style1 .item .img-holder img {
    transition: all 0.5s ease 0s;
    object-fit: fill;
}

.event-style1 .item .overlay {
    background-color: rgba(16, 37, 46, 0.9);
    text-align: center;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    color: #fff;
    opacity: 0;
    filter: alpha(opacity=0);
    transition: all 450ms ease-out 0s;
    transform: rotateY(180deg) scale(0.5, 0.5);
}

.event-style1 .item:hover .img-holder .overlay {
    opacity: 1;
    filter: alpha(opacity=100);
    transform: rotateY(0deg) scale(1, 1);
}

.event-style1 .item .overlay .box {
    display: table;
    height: 100%;
    width: 100%;
}

.event-style1 .item .overlay .box .content {
    display: table-cell;
    text-align: center;
    vertical-align: middle;
}

.event-style1 .item .img-holder .overlay .box .content a i {
    background: #8dc63f;
    border-radius: 50%;
    color: #ffffff;
    display: inline-block;
    font-size: 16px;
    height: 45px;
    line-height: 48px;
    transition: all 500ms ease 0s;
    width: 45px;
}

.event-style1 .item .img-holder .overlay .box .content a:hover i {
    background: #8dc63f;
}

.event-style1 .owl-theme .owl-nav {
    padding: 0;
    position: absolute;
    right: 50%;
    margin: 0;
    margin-right: -51px;
}

.event-style1 .owl-theme .owl-nav [class*=owl-] {
    color: #303030;
    font-size: 15px;
    margin: 0 5px;
    padding: 0;
    background-color: transparent;
    height: 45px;
    border-radius: 50%;
    width: 45px;
    line-height: 41px;
    border: 2px solid #f4f4f4;
    font-weight: 700;
    text-align: center;
    margin-left: 1px;
    display: inline-block;
    transition: color .3s ease;
}

.event-style1 .owl-theme .owl-nav [class*=owl-]:hover {
    color: #8dc63f;
    transition: .5s ease;
}


